<!--
 * @Description: 产品主页
 -->
 <template>
  <div id="association-default">
    <!--左边长列表-->
    <div class="box1-left">
      <currentLocate></currentLocate>
      <!-- <div class="box2-left"> -->
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('产品推荐')" :widgetType=2></pb-list>
        </keep-alive>
      </div>
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('产品新闻')" :widgetType=2></pb-list>
        </keep-alive>
      </div>
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('产品评测')" :widgetType=2></pb-list>
        </keep-alive>
      </div>
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('产品库')" :widgetType=2></pb-list>
        </keep-alive>
      </div>
      <!-- </div> -->
    </div>
    <!--右边长列表-->
    <div class="box4-right">
      <div class="box5-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs3" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
      <div class="box5-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs2[1]" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
      <div class="box6-right">
        <user-box></user-box>
      </div>
      <div class="box7-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs2[0]" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
      <div class="box12-right">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">产品库搜索</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-search></pb-search>
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>
 <script>
import UserBox from "@/components/common/tools/user-box";
import slide2 from "@/components/common/tools/slide2";
import PbSearch from "@/components/common/tools/pb-search";
import currentLocate from "@/components/common/tools/currentLocate";
import PbList from "@/components/common/tools/pb-list";
import { proofreading, getHotProduct } from "@/api/getData";

export default {
  name: "association-default",
  data() {
    return {
      tabs: global.tabs,
      tabs3: [
        {
          name: "热门产品",
          belong: "产品", //归属的小标题
          path: "", //点击跳转
          type: 1, //类别：春列表 //子组件名
          message: null, //组件内容，对象数组
          requestPath: "", //文章列表url
          newsUrl: "/product/getProductInfo" //具体文章请求url
        }
      ]
    };
  },
  filters: {
    FormatDate: function(value, type) {
      if (type) {
        value = value.filter(item => {
          if (item.name == type) return true;
        });
      }
      return value;
    }
  },
  computed: {
    tabs2() {
      let value = global.tabs;
      let value1, value2;
      value1 = value.filter(item => {
        if (item.belong == "活动") return true;
      });
      value2 = value.filter(item => {
        if (item.belong == "相关热点") return true;
      });
      if (!value[0].message && !value[0].message && !value[0].message) {
        proofreading.call(this); //数据未加载，调用函数校对加载
      }
      value = [value1, value2];
      return value;
    }
  },
  methods: {
    init() {
      //显示路径
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == "产品"
      );
      global.currentPath.data = [index];
    }
  },
  activated() {
    this.init();
  },
  mounted() {
    let that = this;
    getHotProduct(this.HOST)
      .then(res => {
        if (res.result == "error") {
          that.$message.error({
            showClose: true,
            message: "获取热门产品信息异常",
            duration: 2000
          });
        } else {
          let message = res.message.map(item => {
            item.title = item.name;
            return item;
          });
          that.$set(
            that.tabs3,
            that.tabs3[0],
            (that.tabs3[0].message = message)
          );
        }
      })
      .catch(err => {
        that.$message.error({
          showClose: true,
          message: "获取热门产品信息异常",
          duration: 2000
        });
      });
  },
  components: {
    UserBox,
    slide2,
    PbList,
    currentLocate,
    PbSearch
  }
};
</script>
<style lang="scss" scoped>
#association-default /deep/ img {
  max-width: 650px;
}
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
}
@mixin border {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
}
#association-default {
  @include adv;
  height: 100%;
}
/*右边小框重复样式*/
@mixin litter_one_tab_box {
  @include i-box;
  @include border;
  height: 312px;
  width: 390px;
  float: right;
  margin-bottom: 12px;
}
.hot_search {
  @include adv;
  height: 100%;
  //margin-left:15px;
  padding: 13px;
  background: rgba(255, 255, 255, 0.95);
}
.box1-left {
  margin-top: 20px;
  width: 790px;
  float: left;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
  margin-bottom: 20px;
}
.box2-left {
  //@include i-box;
  //height: 50px;
  margin: 10px 0;
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
}
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
}

/*左边小部件样式*/
.box3-left {
  @include i-box;
  min-height: 312px;
  margin-top: 12px;
  background: rgba(255, 255, 255, 0.95);
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
}
.box4-right {
  margin-top: 20px;
  width: 390px;
  //@include i-box;
  float: right;
}
.box4-right div {
  margin-right: 0.5px;
}
.box12-right,
.box5-right,
.box6-right,
.box7-right {
  @include litter_one_tab_box;
  background: rgba(255, 255, 255, 0.95);
}
</style>



